@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans&family=Seymour+One&display=swap');
* {
  box-sizing: border-box;
}
body {
  background-color: black;
  color: white;
  margin: 0;
  font-family: 'Nunito Sans', sans-serif;
}
.LevelSelect {
  
}
.LevelList {
  display: flex;
}
.LevelCard {
  height: 20rem;
  width: 15rem;
  margin: 2rem;
  color: black;
  border-radius: 1rem;
  padding-top: 2rem;
  transition: transform 150ms;
}
.LevelCard:hover {
  box-shadow: rgba(3, 102, 214, 0.4) 0px 5px, rgba(23, 102, 214, 0.3) 0px 10px, rgba(3, 102, 214, 0.2) 0px 15px, rgba(3, 102, 214, 0.1) 0px 20px, rgba(3, 102, 214, 0.05) 0px 25px;
  transform:scale(1.05) translateY(-10px);
}
.LevelCardTitle {
  color: white;
  background-color: black;
  margin: 1rem 0 1rem 0;
  border: white solid 1px;
  text-align: center;
  border-left: none;
  border-right: none;
  background-color: rgb(6, 9, 40);
  box-shadow: rgba(3, 102, 214, 0.3) 0px 0px 0px 3px;
}
a {
  color: inherit; 
  text-decoration: inherit; 
}
.ClickMenu {
  backdrop-filter: blur(10px);
  background-color: rgba(0,0,0,.5);
  border: white solid .2vh;
  background-color: none;
  position: absolute;
  border-radius: .8vh;
  user-select: none;
  text-align: center;
}
.ClickMenu > div {
  font-size: 2.25vh;
  padding: 1vh 3vh 1vh 3vh;
}
.ClickMenu > div:hover {
  background-color: rgba(0,0,0,.2);
}
.ClickMenu > div:first-child {
  padding-top: .8h;
  border-bottom: solid white .2vh;
  border-top-left-radius: .8vh;
  border-top-right-radius: .8vh;
}
.ClickMenu > div:last-child {
  padding-bottom: .8vh;
  border-top: solid white .2vh;
  border-bottom-left-radius: .8vh;
  border-bottom-right-radius: .8vh;
}
.Cursor {
  position: absolute;
  height: 7vh;
  width: 7vh;
  border-radius: 10vh;
  border: rgb(185, 0, 0) dashed .4vh;
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
}
.Cursor::after {
  content: " ";
  position: absolute;
  display: block;
  background-color: white;
  height: .8vh;
  width: .8vh;
  top: calc(50% - .4vh);
  left: calc(50% - .4vh);
  border-radius: .8vh;
  z-index: 9;
}
.Char {
  color: white;
}
.DarkenBG {
  position: fixed;
  height: 100%;
  width: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
  top: 0;
  left: 0;
}
.WinScreen {
  background-color: black;
  width: 50vh;
  height: 50vh;
  position: absolute;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  font-size: 4vh;
  padding: 4vh;
  text-align: center;
}
.StopWatch {
  position: fixed;
  top: 0;
  left: 0;
  background-color: black;
  font-size: 8vh;
}
#Name, #Name::placeholder {
  width: 20vh;
  font-size: 2vh;
  text-align: center; 
  height: 5vh;
}
#SubBtn {
  display: inline-block;
  background-color: rgb(73, 73, 73);
  border: solid white .2vh;
  user-select: none;
  font-size: 2.5vh;
  transform: translateY(.1vh);
}
#SubBtn > div {
  margin: .85vh;
}
#SubBtn:hover {
  background-color: rgb(184, 184, 184);
}
.LeaderBoard {
 background-color:rgb(2, 4, 21);
}
.LeaderBoard {
  color: white;
  padding-left: 1rem;
  overflow-x: hidden;
  overflow: scroll;
}
.LeaderBoard::-webkit-scrollbar {
  display: none;
}
.LeaderBoard {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
.Entry > div{
  display: inline-block;
  margin-top: .25rem;
  margin-bottom: .25rem;
}
.Credit {
  color: lightblue;
}
.Credit:hover {
  color: skyblue;
}
.LevelSelect > h1 {
  text-align: center;
  font-family: 'Seymour One', sans-serif;
}